  .login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 20px;
    margin: 0;
  }
  .login-container h1{
    font-size: 40px;
  }
    
  .login-form {
    background-color: rgb(231, 231, 240);
    backdrop-filter: blur(80px);
    display: flex;
    flex-direction: column;
    max-width: 600px;
    margin: 30px;
    padding: 30px;
    border: 2px solid gray;
    border-radius: 8px;
    justify-content: space-between;
  }
    
  label {
    margin-bottom: 5px;
    font-weight: bold;
    padding-top: 10px;
  }
    
  input {
    margin-bottom: 10px;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 8px;
    width: 300px;
  }
    
  .loginbutton {
    background: transparent;
    color: rgb(0, 0, 0);   
    padding: 5px;
    border: none;
    cursor: pointer;
    border-radius: 8px;
    margin-top: 30px;
    background-color: gray;
    font-weight: bold;
    font-size: 20px;
  }
  
  .loginbutton:hover{
    background-color: rgb(16, 188, 194);
  }

  @media (max-width: 768px) {
    .login-container{
      margin: 0;
    }
    .login-container h1 {
        text-align: center;
        font-size: 28px; 
    }

    .login-form {
        margin: 10px; 
        padding: 20px; 
        width: 85%; 
    }

    input {
        width: 90%; 
        padding: 10px; 
    }

    .loginbutton {
        font-size: 18px; 
        padding: 10px; 
    }
}